/* src/components/Contact/Contact.css */
.contact {
    padding: 4rem 2rem;
    background-color: #fff;
    text-align: center;
  }
  
  .contact h2 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }
  
  .contact p {
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 1rem;
  }
  
  .contact a {
    color: #007bff;
    text-decoration: none;
  }
  
  .contact a:hover {
    text-decoration: underline;
  }
  